body {
  margin: 0;
  font-family: 'Oxygen', sans-serif;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1 {
  font-size: 3rem;
  font-weight: weight;
}

h2 {
  font-size: 2rem;
  font-weight: bold;
}

h3 {
  font-size: 1.5rem;
  font-weight: bold;
}

h4 {
  font-size: 1.25rem;
  font-weight: bold;
}

h5 {
  font-size: 1rem;
  font-weight: bold;
}

p, span, ul, li {
  font-size: 0.9rem;
}


.container {
  height: 80vh;
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.info {
  flex: 0 1 auto;
  padding : 5px;
}

.action {
  flex: 1 1 35%;
  padding : 5px;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align : left;
}

li {
  text-align : left;
}

hr {
  border: 1px dashed gray;
}
.messages {
  border: 1px dashed lightgray;
  border-radius : 20px;
  width:25ch;
  height: calc(7*0.9rem);
  margin-top:10px;
  margin-left:auto;
  margin-right:auto;
  overflow:hidden;
  padding: 5px;
}

.messages ul {
  text-align: center;
}

.messages li {
  text-align: center;
}

.resource_div {
  border: 1px dashed lightsalmon;
  border-radius : 20px;
  width:25ch;
  margin-top:10px;
  margin-bottom:10px;
  margin-left:auto;
  margin-right:auto;
  overflow:hidden;
  padding: 5px;
}

.building_div {
  border: 1px dashed lightpink;
  border-radius : 20px;
  width:25ch;
  margin-top:10px;
  margin-bottom:10px;
  margin-left:auto;
  margin-right:auto;
  overflow:hidden;
  padding: 5px;
}

button {
  margin: 2px;
  padding: 3px;
  width: 20ch;
  border-radius:10px;
}